import { RootStore } from "../store";
var HASH_HANDLER_PLUGIN_UPDATE_MODAL_PREFIX = "rpm-wp-client-plugin-update-";
/**
 * Listen to window hash links and open the license modal.
 */

function listenHashPluginUpdate() {
  var hash = window.location.hash;

  if (hash.startsWith("#".concat(HASH_HANDLER_PLUGIN_UPDATE_MODAL_PREFIX))) {
    var slug = hash.substr(HASH_HANDLER_PLUGIN_UPDATE_MODAL_PREFIX.length + 1);
    RootStore.get.pluginUpdateStore.setFromSlug(slug, true); // Reset, should not be visible in URL

    window.location.hash = "";
  }
}

export { listenHashPluginUpdate, HASH_HANDLER_PLUGIN_UPDATE_MODAL_PREFIX };